.button {
  margin-right: 15px;
}

.container {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.button_design {
  color: #fff;
  cursor: pointer;
  z-index: 0;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  background: #111;
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 18px 48px;
  font-size: 18px;
  position: relative;
}

.button_design:before {
  content: "";
  z-index: -1;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  background: linear-gradient(45deg, red, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, red) 0 0 / 400%;
  border-radius: 10px;
  transition: opacity .3s ease-in-out;
  animation: glowing-button-85 20s linear infinite;
  position: absolute;
  top: -2px;
  left: -2px;
}

@keyframes glowing-button-85 {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 400% 0;
  }

  100% {
    background-position: 0 0;
  }
}

.button_design:after {
  z-index: -1;
  content: "";
  width: 100%;
  height: 100%;
  background: #222;
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
}

.button_design:hover {
  color: silver;
}

